<template>
<div class="page-home">
  <appheader/>
  <div class="page">
    <div class="container-fluid">
      <div class="row">
<profile/>
        <div class="col-lg-9">
          <div class="card card-shadow">
            <router-link to="/">Назад</router-link>
            <h1>Добавить заявку</h1>
            <form>
              <div class="form-group">
                <label for="exampleInputEmail1">Заголовок</label>
                <input type="text" class="form-control" id="regtite" v-validate="'required'" name="regtite" v-model="title" placeholder="Заголовок">
              </div>
              <p v-show="errors.has('regtite')" class="login__error">Поле обязательно для заполнения </p>
              <div class="form-group">
                <label for="vendor">Кто нужен</label>
                <input type="type" class="form-control" name="regvendor" v-validate="'required'" v-model="vendor" id="regvendor" placeholder="Кто нужен">
              </div>
              <p v-show="errors.has('regvendor')" class="login__error">Поле обязательно для заполнения </p>
              <div class="form-group">
                <label for="regtext">Текст заявки</label>
                <input type="type" class="form-control" name="regtext" v-validate="'required'" v-model="text" id="regtext" placeholder="Текст заявки">
              </div>
              <p v-show="errors.has('regtext')" class="login__error">Поле обязательно для заполнения </p>
              <div class="form-group form-check">
              </div>
              <button type="submit" class="btn btn-primary" @click.prevent="createRegistering()" :loading="loading">Отправить заявку</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import appheader from '@/components/NavBar.vue'
import profile from '@/components/profile.vue'
export default {
  data() {
    return {
      text: '',
      title: '',
      vendor: ''
    }
  },
  computed: {
    user() {
       return this.$store.getters.user;
    },
    loading() {
return this.$store.getters.loading
    }
  },
  methods: {
    createRegistering() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          const product = {
            title: this.title,
            vendor: this.vendor,
            text: this.text,
          }
          this.$store.dispatch('createProduct', product)
          .then(() => {
            this.$router.push('/')
          } ) .catch (() => {})
        }
      });
    }
  },
  components: {
    appheader,
    profile
  }
}
</script>
